.searchPage{
    width: 100vw;
    border: 1px solid black;
    background-color: rgb(240, 244, 247);
    height: 100vh;
    position: fixed;
    top: 0;
    left: 0;
    overflow-y: auto;
}
.searchPageInner{
    min-width: calc(100% - 560px);
    min-height: calc(100vh - 120px);
    margin-right: 280px;
    margin-left: 300px;
    margin-top: 80px;
    background-color: white;
    border-radius: 5px;
    padding: 40px 20px;
    box-sizing: border-box;
    box-shadow: 0 5px 10px rgba(0, 0, 0, 0.2);
    overflow-y: auto;
    margin-bottom: 40px;
    position: relative;
    transition: 1000ms;
}
.searchPageInnerMobile{
    margin-left: 40px;
    margin-top: 60px;
    /* width: 100px; */
    position: relative;
    padding-bottom: 60px;
}
.searchResultsArea{
    position: relative;
    padding-bottom: 70px;
}
.searchResultsTopBar{
    height: 50px;
    background-color: white;
    line-height: 50px;
    border-bottom: 1px solid rgb(211, 211, 211);
    font-size: 20px;
    position: fixed;
    width: 100vw;
    z-index: 2;
}
.searchPageInnerWider{
    margin-left: 80px;
}
.pagerNumbers{
    color: rgb(78, 77, 77);
    position: absolute;
    top: 5px;
    right: 20px;
    user-select: none;
}
.pagerNumbersBottom{
    top: auto;
    bottom: 0;
}
.pagerNumbersBottomMobile{
    top: auto;
    bottom: 10px;
    width: 100%;
    right: 0;
    left: 0;
}
.rangeButton{
    padding: 5px 10px;
    cursor: pointer;
    user-select: none;
    border: 1px solid black;
    box-shadow: 0 3px 6px rgba(0, 0, 0, 0.534);
    display: inline-block;
    margin-top: 10px;
    transition: 500ms;
    font-size: 16px;
    font-weight: bold;
    background-image: linear-gradient(rgba(128, 128, 128, 0), rgba(128, 128, 128, 0), rgba(128, 128, 128, 0.2));
}
.rangeButton:hover{
    box-shadow: 0 2px 3px rgba(0, 0, 0, 0.534);
    font-size: 15px;
    margin-left: 1px;
    /* padding: 4px 10px; */
}
.rangeButton2{
    padding: 2px 5px;
    cursor: pointer;
    user-select: none;
    /* border: 1px solid black; */
    /* box-shadow: 0 3px 6px rgba(0, 0, 0, 0.534); */
    display: inline-block;
    margin-top: 10px;
    transition: 500ms;
    font-size: 16px;
    font-weight: bold;
    /* background-image: linear-gradient(rgba(128, 128, 128, 0), rgba(128, 128, 128, 0), rgba(128, 128, 128, 0.2)); */
    margin: 10px;
}
.rangeButton2:hover{
    box-shadow: 0 2px 3px rgba(0, 0, 0, 0.534);
    font-size: 15px;
    /* margin-left: 1px; */
    /* padding: 4px 10px; */
}
.rangeButton2Mobile{
    padding: 10px 20px;
    background-color: rgb(219, 219, 219);
    border-radius: 5px;
}
.pagerInputs input{
    width: 40px;
    padding: 10px;
    text-align: center;
}
@media screen and (max-width: 1650px) {
    /* .searchResultsArea{
        width: calc(100% - 280px);
    } */
    .searchPageInner{
        margin-left: 300px;
        margin-right: 30px;
    }
    .searchPageInnerWider{
        margin-left: 80px;
    }
}