:root {
  --primarycolor: #D65E4D;
}

.App {
  text-align: center;
  position: relative;
}
.closeButton{
  position: absolute;
  top: 5px;
  right: 5px;
  height: 20px;
  width: 20px;
  background-color: rgb(243, 125, 125);
  border-radius: 3px;
  cursor: pointer;
  color: white;
  /* display: flex;
  justify-content: center;
  align-items: center; */
  transition: 500ms;
  line-height: 16px;
  z-index: 2;
  text-align: center;
}
.closeButton:hover{
  background-color: rgb(255, 96, 96);
  box-shadow: 0 0 10px rgb(247, 199, 199);
}
.minMaxButton{
  position: absolute;
  top: 3px;
  right: 35px;
  height: 25px;
  width: 25px;
  cursor: pointer;
  z-index: 2;
  transition: 500ms;
}
.minMaxButton:hover{
  background-color: rgb(219, 240, 255);
  box-shadow: 0 0 5px rgba(164, 219, 255, 0.548);

}
input {
  outline: none;
  border: none;
  padding: 8px 20px;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.8) inset;    
  margin: 10px;
  vertical-align: top;
  box-sizing: border-box;

}
textarea {
  outline: none;
  border: none;
  padding: 5px 10px;
  box-sizing: border-box;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.8) inset;    
  margin: 10px;
  vertical-align: top;
}
select {
  outline: none;
  border: none;
  padding: 8px 20px;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.8) inset;    
  margin: 10px;
  vertical-align: top;
  width: calc(100% - 12px);
  box-sizing: border-box;

}
img{
  height: 100%;
  width: 100%;
  object-fit: cover;
}
pre{
  white-space: pre-wrap;
  text-align: left;
}
.page{
  background-color: rgba(163, 149, 144, 0.123);  
  padding: 0 10%;
  min-height: 200px;
  background-color: blue;
}
.pageInner{
  width: 100%;    
  min-height: 200px;
  background-color: white;
}
.bottomButtons{
  width: calc(100% - 10px);
  display: flex;
  justify-content: space-between;
  position: absolute;
  bottom: 5px;
  left: 5px;
}
.hidden{
  visibility: hidden;
  height: 0;
  width: 0;
  position: absolute;
}
.window{
  position: fixed;
  top: 200px;
  left: 0;
  right: 0;  
  margin-left: auto;
  margin-right: auto;
  overflow: auto;
  width: 800px;
  min-height: 200px;
  background-color: rgb(255, 255, 255);
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.5);
  border-radius: 5px;
  padding: 10px;
  box-sizing: border-box;
  z-index: 4;
}
.devPage{
  height: 100vh;
  width: 100%;
  background-color: rgb(40, 40, 46);
}
image{
  height: 100%;
  width: 100%;
  object-fit: cover;
}
button{
  width: 30%;
  padding: 5px 10px;
  background-color: rgb(243, 243, 243);
  background-image: linear-gradient(rgba(128, 128, 128, 0), rgba(201, 201, 201, 0.699));
  border-radius: 3px;
  box-shadow: 0 3px 5px rgba(0, 0, 0, 0.3);
  outline: none;
  border: none;
  margin: 10px;
  transition: 500ms;
  cursor: pointer;
}
button:hover{   
  background-color: rgb(250, 250, 250);     
  box-shadow: 0 2px 3px rgba(0, 0, 0, 0.3);
  outline: none;
  border: none;
  margin: 10px;
}
a{
  text-decoration: none;
  color: black;
}
.linkButton{
  width: calc(100% - 10px);
  background-color: rgb(241, 241, 241);
  background-image: linear-gradient(rgba(248, 248, 248, 0), rgba(175, 175, 175, 0.411));
  display: inline-block;
  margin-top: 10px;
  padding: 5px;
  box-sizing: border-box;
  text-decoration: none;
  color: black;
  border-radius: 5px;
}
@media screen and (max-width: 1200px){
  .page{
    padding: 0 2%;
  }  
}