.menuWindow{
    padding-bottom: 40px;
}
.menuWindowMobile{
    top: auto;
    bottom: 100px;
    width: 90vw;
    z-index: 9;
    text-align: center;
    padding-bottom: 40px;
}
.menuButton{
    width: 95%;
    display: inline-block;
    border-radius: 5px;
    font-size: 20px;
    transition: 500ms;
    padding: 10px 20px;
    background-color: rgb(235, 241, 247);
    margin-top: 10px;
    border: 1px solid rgb(61, 138, 253);
    box-sizing: border-box;
}
.menuButton:hover{
    background-color: rgb(223, 223, 223);
}
.menuTitle{
    width: 100%;
    height: 60px;
    line-height: 60px;
    border-bottom: 1px solid black;
    font-size: 30px;
}