.mobileWindow{
    height: 600px;
    width: 350px;
    background-color: grey;
    border-radius: 10px;
    display: inline-block;
    overflow: hidden;
}
.mobileWindowInner{
    height: 100%;
    width: 100%;
    overflow-y: auto;
    position: relative;
}
.desktopWindow{
    height: 600px;
    width: 800px;
    background-color: grey;
}
.mobileWindowContainer{
    width: 100%;
    text-align: center;
    padding: 20px 0;
}