.editProfilewindow{
    width: 850px;
}
.editProfilewindowMobile{
    height: 80vh;
    overflow: hidden;
    width: 90vw;
    position: fixed;
    top: 80px;
    padding: 0;
}
.editProfileInner{
    height: calc(100% - 100px);
    overflow-y: auto;
    padding-bottom: 40px;
}
.editPofileTitle{
    margin: 0;
    padding: 0;
    font-size: 25px;
    height: 32px;
}
.editPofileTitleMobile{
    padding: 10px 0;
}
.editProfileAreaTitle{
    font-size: 20px;
}
.flexBox{
    display: flex;
    justify-content: space-around;
}
/* Basic Account Input Area */

.editProfileBio{
    height: 235px;
}
.inputArea{
    width: 40%;
    display: inline-block;
    vertical-align: top;    
    margin-top: 10px;
}
.inputAreaMobile{
    width: 100%;
    padding: 10px;
    box-sizing: border-box;
}
.proflieInputHalf{
    width: 50%;
    display: inline-block;
    padding: 0 5px;
    box-sizing: border-box;
}
.proflieInputWhole{
    width: 100%;
    display: inline-block;
    padding: 0 5px;
    box-sizing: border-box;
}
.profileInputField{
    width: 100%;
    height: 100%;
    display: inline-block;
    text-align: left;
    margin-bottom: 5px;
}
.profileInputField input{
    width: 100%;
    margin: 0;
}
.profileInputField textarea{
    min-width: 100%;
    max-width: 100%;
    margin: 0;
    height: 100%;
    padding: 10px;
}
.inputArea input.inputFullWidth{
    width: calc(100% - 60px);
}

/* Create Account Area */
.createAccountsArea{
    border: 1px solid rgba(0, 0, 0, 0.2);
    padding: 10px;
    margin: 10px;
    width: 730  px;
}
.createAccountTitle{
    font-size: 26px;
}
.createAccountText{
    font-size: 14px;
    color: grey;
    margin: 10px;
}
.createAccountsArea input{
    margin: 10px;
    width: 90%;
}
.createAccountButton{
    width: calc(100% - 40px);
    background-color: white;
    background-image: linear-gradient(rgba(128, 128, 128, 0), rgba(209, 207, 207, 0.171));
    padding: 10px;
}