.adminPage{
    background-color: rgb(185, 192, 201);
    min-height: 100vh;
    padding-top: 80px;
    padding-bottom: 40px;
    box-sizing: border-box;
}
.adminArea{
    background-color: white;
    width: calc(45% - 20px);
    height: 400px;
    border-radius: 5px;
    box-shadow: 0 5px 10px rgba(0, 0, 0, 0.5);
    margin: 20px;
    overflow: auto;
    position: relative;
    display: inline-block;
}
.adminAreaTitle{
    width: 100%;
    height: 60px;
    line-height: 60px;
    border-bottom: 1px solid black;
    font-size: 30px;
}
.adminAreaUsers{
    background-color: white;
    width: 90%;
    margin-left: 5%;
    border-radius: 5px;
    box-shadow: 0 5px 10px rgba(0, 0, 0, 0.5);
    overflow: auto;
    position: relative;
    box-sizing: border-box;
    height: 800px;
}
.adminAreaUsersInner{
    padding-bottom: 40px;
    padding-left: 220px;
    position: relative;
}
.adminFilter{
    position: absolute;
    height: 100%;
}

.editUserWindow{
    padding-bottom: 40px;
}
.confirmationWindow{
    height: 140px;
    min-height: 140px;
    width: 300px;
    padding: 20px;
    box-sizing: border-box;
}
.usersArea{
    width: 90%;
    min-height: 200px;
    border: 1px solid rgb(146, 184, 253);
    padding: 10px;
    box-sizing: border-box;
    display: inline-block;
}
.userTile{
    height: 320px;
    width: 280px;
    margin: 10px;
    box-sizing: border-box;
    border: 1px solid black;
    display: inline-block;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.5);
    cursor: pointer;
    transition: 500ms;
    vertical-align: top;  
    position: relative;  
    filter: brightness(.9) grayscale(.1);
}

.userTile:hover{
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.4);
    margin-top: 0px;
    margin: 5px;
    width: 290px;
    height: 330px;
    font-size: 17px;  
    filter: brightness(1) grayscale(0);
}
.userTileMobile{
    width: calc(100% - 20px);
    max-width: 330px;
    height: 360px;
    filter: none;
}
.userTileMobile:hover{
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.4);
    margin-top: 0px;
    margin: 5px;
    font-size: 16px;  
    filter: brightness(1) grayscale(0);
    width: calc(100% - 0px);
    max-width: 340px;
    height: 370px;
    width: calc(100% - 20px);
    max-width: 330px;
    height: 360px;
    margin: 10px;
}
.userTileText{
    position: absolute;
    bottom: 0px;
    width: 100%;
    color: white;
    /* background-color: rgba(25, 140, 247, 0.26); */
    /* background-image: radial-gradient(rgba(25, 140, 247, 0.678), rgba(0, 0, 255, 0)); */
    /* box-shadow: 0 0 10px rgba(25, 139, 247, 0.432); */
    /* z-index: 2; */
    min-height: 55px;
    font-weight: bold;
    /* text-shadow: 0px 0px 5px rgb(1, 84, 179); */
    /* filter: text-shadow(color=#1d1dab, offx=0, offy=0); */
    background-color: rgba(25, 98, 255, 0.452);
    padding: 5px;
    box-sizing: border-box;
    
}
.userTileImage{
    position: absolute;
    left: 0;
    top: 0;
    height: 100%;
    width: 100%;
    /* background-color: gray; */
}
.userTileImage img{    
    height: 100%;
    width: 100%;
    object-fit: cover;
}
.activeBanner{
    position: absolute;
    bottom: 60px;
    left: 5px;
    color: white;
    background-color: rgba(31, 182, 31, 0.5);
    padding: 5px 10px 2px 5px;
    border-radius: 20px;
}
.activeBannerCircle{
    height: 20px;
    width: 20px;
    margin-right: 5px;
    border-radius: 50%;
    background-color: rgba(31, 182, 31);
    display: inline-block;
}
.activeBannerCircleText{
    vertical-align: top;
    display: inline-block;
    height: 20px;
}
.newUserTile{

    line-height: 200px;  
    font-size: 20px;
    text-align: center;
    font-weight: bold;
}
.newUserTile:hover{
    line-height: 280px;    
    font-size: 30px;
}